
body {
  margin: 0;
  font-family: system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
background-color: rgba(235, 152, 232, 0.459);
}

*{
font-family: 'Enter', system-ui;
}

.wrapper {
  /* Rectangle 17 */
background: #FFFFFF;
box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
border-radius: 20px;

max-width: 1080px;
margin: 50px auto;
}

header{
 border-bottom: 1px solid rgb(240, 240, 240);
  img{
    margin-right: 15px;
  }

      h3,p{
        margin:0;

      }
}

.content{
  h1{
    margin: 0;
  }
}

.search-block {
  border: 1px solid #F3F3F3;
  border-radius: 10px;
  padding: 0 15px;
position: relative;
  .clear{
    position: absolute;
    right: 9px;
    width: 18px;
    height: 18px;
    top: 14px;
  }
 input{
   border: none;
   padding: 13px;
   font-size: 16px;
   width: 200px;
    outline: none;
 }
}




.cartTotalBlock{
  

  ul{
    margin-top: 0;
    padding-inline-start: 0px;
  display: block;
  margin-bottom: 50px !important;
  li{
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;

    div{
      flex:1;
      height: 1px;
      border-bottom: 1px dashed #dfdfdf ;
      position: relative;
      top:-4px;
      margin: 0 7px;
    }
    
  }
  
}
.greenButton{
  position: relative;
  margin-bottom: 30px;
  &:disabled {
    animation:button-loading 0.2s ease-in-out infinite;
  }

  &:hover{
    img{
      transform: translateX(5px);
    }

  }
  
  img{
    position: absolute;
    right: 30px;
    top: 20px;
    transition: transform 0.2s ease-in-out;
  }
}
}


    


.greenButton{
  
    width: 100%;
    height: 55px;
    background: #9DD558;
    border-radius: 18px;
    border: 0;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.15s ease-in-out;
    
    &:disabled{
      background-color: #bebebe !important;
      cursor: default;
    };

    &:hover{
      background: lighten(#9dd558, 5%);
    }
    
    &:active{
      background: darken(#9dd558, 5%);
    }
  
 

}


.cartItem{

  border: 1px solid #F3F3F3;
  border-radius: 20px;
  overflow: hidden;
  padding: 0 20px;

  .cartItemImg{
    height: 100px;
    width: 70px;
    background-size: contain;
    background-position: 0 0;
    background-repeat: no-repeat;
    margin-right: 20px;
  }

  p{
    font-size: 16px;
    margin: 0;
  }

  b{
    font-size: 14px;
  }

  .removeBtn{
    opacity: 0.5;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;

    &:hover{
      opacity: 1;
    }
  }

  .cartEmpty{
    text-align: center;
    p{
      width: 280px;
      line-height: 24px;
    }

    .greenButton{
      width: 245px;
      margin-top: 20px;

      &:hover{
        img{
          transform: rotate(180deg) translateX(3px);
        };
      }
      img{
        position: relative;
        top: 1px;
        transform: rotate(180deg);
        margin-right: 15px;
        transition: transform 0.15s ease-in-out ;
      }
    }
  }
}

@keyframes button-loading {
  0%{
    opacity: 1;
  }
  50%{
    opacity: 0.7;
  }
  100%{
    opacity: 1;
  }

}